import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Container } from '@material-ui/core';
import Article from './Article.js'

function App() {

  return (
    <Router>
      <Container>
        <Switch>
          <Route exact path="/article/:article">
            <Article />
          </Route>
        </Switch>
      </Container>
    </Router>
  );
}

export default App;
