import React, { lazy, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';


const importArticle = (article) =>
  lazy(() =>
    import(`./artigos/${article}.js`).catch(() =>
      //import(`./views/NullView`)
      console.log("deu pau na hora de pegar o artigo")
    )
  );


const Article = () => {
    const urlParams = useParams();

    const [articlePage, setArticlePage] = useState([]);

    useEffect(() => {
      async function loadViews() {
        const componentPromise =
        (
            async (article) => {
                const View = await importArticle(article);
                return <View key={1} />;
            }
        )(urlParams.article);
  
        Promise.resolve(componentPromise).then(setArticlePage);
      }
  
      loadViews();
    }, []);
  
    return (
        <React.Suspense fallback='Loading views...'>
            {articlePage}
        </React.Suspense>
    );
}

export default Article